    .pointer-coarse\:md\:overflow-y-auto{
      overflow-y: auto;
    }
  }
}
@media (min-width: 768px){

  .translucent-ui .translucent-ui\:md\:border-0{
    border-width: 0px;
  }

  .translucent-ui .md\:translucent-ui\:border-\[--popover-border-color\]{
    border-color: var(--popover-border-color);
  }

  .translucent-ui .md\:translucent-ui\:bg-default{
    background-color: var(--sn-stylekit-background-color);
  }

  .translucent-ui .md\:translucent-ui\:bg-transparent{
    background-color: transparent;
  }

  .translucent-ui .enabled\:hover\:md\:translucent-ui\:border-\[--popover-border-color\]:hover:enabled{
    border-color: var(--popover-border-color);
  }
}
@media (min-width: 1024px){

  .lg\:mr-12{
    margin-right: 3rem;
  }

  .lg\:block{
    display: block;
  }

  .lg\:h-6{
    height: 1.5rem;
  }

  .lg\:w-6{
    width: 1.5rem;
  }

  .lg\:pt-0{
    padding-top: 0px;
  }

  .lg\:text-base{
    font-size: 1rem;
    line-height: 1.5rem;
  }

  .lg\:text-lg{
    font-size: 1.125rem;
    line-height: 1.75rem;
  }

  .lg\:text-menu-item{
    font-size: 0.813rem;
  }

  .lg\:text-navigation-list-item{
    font-size: 0.88rem;
  }

  .lg\:text-sm{
    font-size: 0.875rem;
    line-height: 1.25rem;
  }

  .lg\:text-xs{
    font-size: 0.75rem;
    line-height: 1rem;
  }

  .lg\:leading-\[1\.3\]{
    line-height: 1.3;
  }
}
@media (min-width: 1280px){

  .xl\:items-center{
    align-items: center;
  }
}
@media (pointer: coarse){

  @media (min-width: 768px) and (max-width: 1023px){

    .pointer-coarse\:md-only\:hidden{
      display: none;
    }
  }

  @media (min-width: 1024px) and (max-width: 1279px){

    .pointer-coarse\:lg-only\:hidden{
      display: none;
    }
  }
}
.\[\&\:\:-webkit-scrollbar\]\:bg-transparent::-webkit-scrollbar{
  background-color: transparent;
}
.\[\&\:not\(\:first-child\)\]\:ml-2:not(:first-child){
  margin-left: 0.5rem;
}
.\[\&\:not\(\:first-child\)\]\:ml-3:not(:first-child){
  margin-left: 0.75rem;
}
.\[\&\>\*\]\:align-middle>*{
  vertical-align: middle;
}
@media (min-width: 640px){

  .sm\:\[\&\>\*\]\:mx-\[var\(--editor-margin\)\]>*{
    margin-left: var(--editor-margin);
    margin-right: var(--editor-margin);
  }

  .sm\:\[\&\>\*\]\:max-w-\[var\(--editor-max-width\)\]>*{
    max-width: var(--editor-max-width);
  }
}
.\[\&\>\[data-active-item\]\]\:bg-passive-5>[data-active-item]{
  background-color: var(--sn-stylekit-passive-color-5);
}
.\[\&\>path\]\:\!text-current>path{
  color: currentColor !important;
}
.\[\&\>svg\]\:fill-current>svg{
  fill: currentColor;
}
.\[\&\[data-active-item\]\]\:bg-info[data-active-item]{
  background-color: var(--sn-stylekit-info-color);
}
.\[\&\[data-active-item\]\]\:bg-info-backdrop[data-active-item]{
  background-color: var(--sn-stylekit-info-backdrop-color);
}
.\[\&\[data-active-item\]\]\:text-info-contrast[data-active-item]{
  color: var(--sn-stylekit-info-contrast-color);
}
.\[\&\[data-enter\]\]\:opacity-100[data-enter]{
  opacity: 1;
}
@media (min-width: 768px){

  .\[\&\[data-enter\]\]\:md\:opacity-75[data-enter]{
    opacity: 0.75;
  }
}
.\[\&\[data-leave\]\]\:opacity-60[data-leave]{
  opacity: 0.6;
}
.\[\&_code\]\:rounded code{
  border-radius: 0.25rem;
}
.\[\&_code\]\:bg-default code{
  background-color: var(--sn-stylekit-background-color);
}
.\[\&_code\]\:px-1\.5 code{
  padding-left: 0.375rem;
  padding-right: 0.375rem;
}
.\[\&_code\]\:py-1 code{
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}
